.liBtn{}
body{
    background: #0D1323;
}
main, .title{
    display: flex;
}
.title{
    justify-content: space-between;
    align-items: center;
}
.title svg:hover path{
    fill: white;
}
main{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.inforsUser{
    background: #5746EA;
    color: white;
    padding: 1.5em 4em 3.7em 1.5em;
    border-radius: .7em;
}
.inforsUser img{
    border: .2em solid white;
    border-radius: 50%;
    width: 3.65em;
    margin-bottom: 1.5em;
}
.title h3, aside ul, .inforsUser h1, .inforsUser p{
    margin: 0;
}
.inforsUser p{
    font-weight: 300;
    font-size: .7em;
    color: rgb(206, 206, 206);
}
.inforsUser h1{
    font-weight: 300;
    font-size: 1.8em;
    letter-spacing: .01em;
}
aside{
    margin-right: 1.5em;
    background: #1D204B;
    border-radius: .7em;
}
aside ul{
    list-style: none;
    padding: 2em 0 0 2em;
    font-size: .8em;
}
aside li{
    color: hsl(235, 45%, 61%);
    cursor: pointer;
}
aside li:focus{
    color: white;
}
aside li:not(:last-child){
    margin-bottom: 1em;
}
.gridInfors{
    display: grid;
    grid-template-columns: repeat(3, 11.5em);
    grid-template-rows: repeat(2, 11.35em);
    gap: 1.2em;
}
.infors{
    border-radius: .7em;
    width: 9.5em;
    padding: 1.5em 1.1em;
    margin-left: -.1em;
    background: #1D204B;
    z-index: 2;
}
[class^="Styles_liBtn"], .infors, .title svg path{
    transition: .4s;
}
.infors:hover{
    cursor: pointer;
    background: #34397B;
}
.imgTop{
    position: absolute;
    top: -.3em;
    right: 1em;
    z-index: -1;
    width: 4.25em;
}
.itemGrid, .divImgTop{
    position: relative;
}
[class^="Styles_itemGrid"]{
    border-radius: 1em 1em 0 0;
    z-index: 1;
    padding-top: .4em;
    height: 8.5em;
}
[class^="Styles_itemGrid"][class$="0"]{
    background: #FF8C60;
}
[class^="Styles_itemGrid"][class$="1"]{
    background: #56C2E6;
}
[class^="Styles_itemGrid"][class$="2"]{
    background: #FF5E7D;
}
[class^="Styles_itemGrid"][class$="3"]{
    background: #4BCF83;
}
[class^="Styles_itemGrid"][class$="4"]{
    background: #7235D1;
}
[class^="Styles_itemGrid"][class$="5"]{
    background: #F1C75B;
}
.imgTop{
    display: inline-block;
}
.title h3{
    color: #FFF;
    font-size: .8em;
    font-weight: 500;
}
h2{
    color: #fff;
    font-size: 2.5em;
    font-weight: 300;
    margin: .3em 0 0 0;
}
.period{
    margin-top: .5em;
    margin-bottom: 0;
    font-size: .7em;
    color: hsl(236, 44%, 75%);
}
.divImgTop{
    height: 2em;
}
[class^="Styles_liBtn"]:hover{
    color: rgb(223, 223, 223);
}
[class^="Styles_liBtn"][class$="true"]{
    color: white;
}
@media screen and (max-width: 51.875em){
    .gridInfors{
        grid-template-columns: repeat(2, 11.5em);
        grid-template-rows: repeat(3, 11.35em);
    }
    .inforsUser{
        padding-top: 5em;
        padding-bottom: 7em;
    }
    .inforsUser img{
        font-size: 1.5em;
    }
    .inforsUser h1{
        font-size: 2em;
    }
    aside ul{
        padding-top: 3em;
    }
    ul li{
        font-size: 1.2em;
    }
}
@media screen and (max-width: 40.3125em){
    main{
        display: block;
        transform: translate(-50%, -30%);
    }
    aside{
        margin: 0 0 1em 0;
    }
    .inforsUser, .infors article:last-child, ul{
        display: flex; 
    }
    ul{
        padding: 1.5em 0 !important;
        justify-content: space-around;
    }
    ul li{
        margin: 0 !important;
    }
    .inforsUser, .infors article:last-child{
        align-items: center;
    }
    .inforsUser{
        padding: 0;
        justify-content: center;
        padding: 1em 0;
    }
    .inforsUser img{
        font-size: 1em;
        margin: 0 1em 0 0;
    }
    .inforsUser h1{
        display: inline;
        font-size: 1.5em;
    }
    .inforsUser article{
        max-width: 5.5em;
    }
    .gridInfors{
        grid-template-columns: 25em;
        grid-template-rows: repeat(6, 9em);
        gap: 2em;
    }
    .infors{
        width: 23em;
    }
    .infors article:last-child{
        justify-content: space-between;
    }
    .infors p{
        font-size: 1em;
    }
}
@media screen and (max-width: 27.8125em){
    .gridInfors{
        grid-template-columns: 20em;
    }
    .infors{
        width: 18em;
    }
}
@media screen and (max-width: 22em){
    .gridInfors{
        grid-template-columns: 17em;
    }
    .infors{
        width: 15em;
    }
}